table {
	border-collapse: collapse;
}
td, th {
	padding: 5px;
}
#currentPaging {
	border: 1px solid #eee;
	margin: 5px;
	padding: 5px 9px;
	color: blue;
	cursor: pointer;
}
#currentPaging:hover{
	text-decoration: underline;
}
#paging {
	color: black;
	margin: 5px;
	padding: 5px;
	cursor: pointer;
}
#paging:hover {
	text-decoration: underline; 
}
.subjectA:link {
	color: black;
	text-decoration: none;
}
.subjectA:visited {
	color: black;
	text-decoration: none;
}
.subjectA:hover {
	color: green;
	text-decoration: underline; 
}
.subjectA:active {
	color: black;
	text-decoration: none;
}